.transactionHistory {
  border-collapse: collapse;
  /* margin: 25px 0; */
  margin-left: auto;
  margin-right: auto;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.transactionHistory thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

.transactionHistory th,
.transactionHistory td {
  padding: 12px 15px;
}

.transactionHistory tbody tr {
  border-bottom: 1px solid #dddddd;
}

.transactionHistory tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.transactionHistory tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}
